<template>
  <div class="container pt-5" style="min-height:100vh;">
    <div class="info-container pb-5">
      <h5 class="text-center pb-3">{{ postData.title }}</h5>
      <span
        v-if="postData.user"
        class="d-flex justify-content-start align-items-center"
      >
        <a href="#user">
          <img
            class="circle left mr-3"
            :src="postData.user.picture"
            width="32"
          />
        </a>
        <strong>{{ postData.user.name }}</strong> </span
      ><br />
      <span><strong>Price:</strong> â‚¡{{ postData.price }}</span>
      <p v-if="postData.category">
        <strong>Category: </strong>{{ getCategory(postData.category) }}
      </p>
      <p class="pb-5">
        <strong>Description:</strong>
        <br />
        {{ postData.description }}
      </p>
      <span class="pb-5">
        <i class="material-icons left">collections</i>
        <strong>Gallery: </strong>
        <span> {{ imagesLength }} images </span>
      </span>
    </div>
    <div class="row">
      <div class="col s12 m6 l4" v-for="(image, index) in images" :key="index">
        <img class="materialboxed" style="width:100%;" :src="image.url" />
      </div>
    </div>
  </div>
  <div class="fixed-action-btn">
    <a class="btn-floating btn-large blue">
      <i class="large material-icons">filter_list</i>
    </a>
    <ul>
      <li>
        <a class="btn-floating red"
          ><i class="material-icons">picture_as_pdf</i></a
        >
      </li>
      <li>
        <a class="btn-floating green"><i class="material-icons">textsms</i></a>
      </li>
    </ul>
  </div>
</template>
<script>
import { mapActions, mapMutations, mapState } from "vuex";
import { useRoute } from "vue-router";
export default {
  name: "Product",
  data() {
    return {
      postID: null,
      postData: {},
      categories: [
        {
          value: 1,
          name: "Art",
        },
        {
          value: 2,
          name: "Electronics",
        },
        {
          value: 3,
          name: "Vehicles",
        },
        {
          value: 3,
          name: "Sports",
        },
        {
          value: 4,
          name: "Real Estate",
        },
      ],
    };
  },
  computed: {
    ...mapState("posts", ["currentPost"]),
    imagesLength() {
      return this.currentPost.images ? this.currentPost.images.length : 0;
    },
    images() {
      return this.currentPost.images ? this.currentPost.images : [];
    },
    media() {
      return this.getMedia();
    },
  },
  methods: {
    ...mapMutations(["toggleLoader"]),
    ...mapActions("posts", ["getPost"]),
    getMedia() {
      var output = [];
      this.images.forEach((image) => {
        output.push(image.url);
      });
      return output;
    },
    getCategory(index) {
      return this.categories[index-1].name;
    },
  },
  mounted() {
    this.toggleLoader();
    const route = useRoute();
    this.postID = route.params.postId;
  },
  watch: {
    postID: function(value) {
      this.getPost(value);
    },
    imagesLength: function(value) {
      this.postData = this.currentPost;
      setTimeout(() => {
        var elems = document.querySelectorAll(".materialboxed");
        M.Materialbox.init(elems);
        var actionsBtn = document.querySelectorAll(".fixed-action-btn");
        M.FloatingActionButton.init(actionsBtn);
      }, 100);
    },
  },
};
</script>
